@use "../global/bootstrap.scss";

.instruction {
  @include bootstrap.offset-sides-y(10px, 50px);

  @media (min-width: bootstrap.$tablet-width) {
    @include bootstrap.offset-sides-y(35px, 47px);
  }

  @media (min-width: bootstrap.$desktop-width) {
    @include bootstrap.offset-sides-y(61px, 61px);
  }
}

.instruction__wrapper {
  width: 280px;
  margin: 0 auto;

  @media (min-width: bootstrap.$tablet-width) {
    width: 708px;
  }

  @media (min-width: bootstrap.$desktop-width) {
    width: 1220px;
  }
}

.instruction__title {
  margin: 0;
  margin-bottom: 40px;
  font-family: bootstrap.$primary-family;
  font-weight: bootstrap.$normal;
  font-size: 36px;
  line-height: 1.11;
  color: bootstrap.$primary-dark;

  @media (min-width: bootstrap.$tablet-width) {
    margin-bottom: 32px;
    font-size: 60px;
    line-height: 1;
  }
}

.instruction__list {
  @include bootstrap.list-reset;
  display: grid;
  gap: 20px;

  @media (min-width: bootstrap.$tablet-width) {
    grid-template-columns: 1fr 1fr;
    column-gap: 153px;
    row-gap: 37px;
  }

  @media (min-width: bootstrap.$desktop-width) {
    grid-template-columns: repeat(4, 1fr);
    column-gap: 80px;
  }
}

.instruction__item {
  display: grid;
  grid-template-columns: 60px auto;
  gap: 20px;

  @media (min-width: bootstrap.$tablet-width) {
    grid-template-columns: auto;
    gap: 31px;
    @include bootstrap.offset-sides-y(40px, 57px);
  }
}

.instruction__picture {
  position: relative;
  width: 60px;
  height: 60px;
  background-color: bootstrap.$green;

  &--leaf::after {
    position: absolute;
    content: url("../img/index-icon/icon-leaf.svg");
    top: 14px;
    left: 15px;
  }

  &--water::after {
    position: absolute;
    content: url("../img/index-icon/icon-water.svg");
    top: 16px;
    left: 17px;
  }

  &--eat::after {
    position: absolute;
    content: url("../img/index-icon/icon-eat.svg");
    top: 15px;
    left: 17px;
  }

  &--watch::after {
    position: absolute;
    content: url("../img/index-icon/icon-watch.svg");
    top: 15px;
    left: 17px;
  }

  @media (min-width: bootstrap.$tablet-width) {
    width: 80px;
    height: 80px;

    &--leaf::after {
      top: 25px;
      left: 24px;
    }

    &--water::after {
      top: 26px;
      left: 27px;
    }

    &--eat::after {
      top: 25px;
      left: 28px;
    }

    &--watch::after {
      top: 25px;
      left: 27px;
    }
  }
}

.instruction__description {
  margin: 0;

  @media (min-width: bootstrap.$tablet-width) {
    font-size: 16px;
    line-height: 1.5;
  }
}
