@use "../global/bootstrap.scss";

.features-list__item {
  display: flex;
  justify-content: space-between;
  font-family: bootstrap.$secondary-family;
  font-weight: bootstrap.$normal;
  font-size: 12px;
  line-height: 1.33;
  color: bootstrap.$dark-grey;

  &:not(:last-child) {
    margin-bottom: 4px;
  }

  @media (min-width: bootstrap.$tablet-width) {
    position: relative;
    font-size: 16px;

    &:not(:last-child) {
      margin-bottom: 14px;
    }

    &::after {
      position: absolute;
      content: "";
      left: 0;
      right: 0;
      bottom: -9px;
      height: 1px;
      background-color: bootstrap.$third-light-grey;
    }
  }
}

.features-list__value {
  margin: 0;
}
